    font-size: 3.7857142857rem;
  }

  .events__descr {
    max-width: 300px;
    font-size: 1rem;
  }

  .events-slider__mobile {
    gap: 1rem;
  }

  .events__cards {
    grid-template-columns: repeat(6, 115px);
  }

  .event {
    border-radius: 1rem;
  }

  .event__info {
    position: absolute;
    top: 0;
    left: 16px;
    max-width: 200px;
  }

  .event__title {
    font-size: 1.2857142857rem;
  }

  .event__button {
