  .contacts__container::before {
    display: none;
  }

  .contacts__room-wrapper {
    padding-left: 20%;
  }

  .contacts__room-wrapper::before {
    width: 28%;
    position: absolute;
    top: 50%;
    left: 0;
    translate: 30% -50%;
    aspect-ratio: 1.6351351351;
    filter: blur(48px);
  }

  .contacts__have-marker {
    position: relative;
    top: unset;
    left: unset;
    width: 85px;
    translate: 0;
    z-index: 0;
  }

  .contacts__lines--moby {
    display: block;
  }

  .contacts-image-wrapper {
    flex-basis: 160px;
  }

  .contacts-image-wrapper::after {
    display: block;
  }

  .contacts-image {
    translate: -15px -12px;
  }

  .contacts__info {
    gap: 36px;
    flex-basis: unset;
  }

  .contacts__title {
    font-size: 3.4285714286rem;
  }

  .contacts__descr {
    font-size: 1rem;
    margin-bottom: 0;
  }

  .contacts__descr::before {
    background: none;
    background-color: #d9d9d9;
    width: 34%;
    aspect-ratio: 1.15625;
    opacity: .06;
    filter: blur(52px);
    position: absolute;
    top: 50%;
    left: 0;
    translate: 25% -50%;
  }

  .contact+.contact {
    margin-top: 19px;
  }

  .contact__text {
    font-size: 1.4285714286rem;
  }

  .footer .container::before {
    display: none;
  }

  .footer {
    padding: 21px 1rem;
  }

  .footer .logo {
    font-size: 25px;
  }

  .footer .logo::before {
    width: 35%;
    aspect-ratio: 3;
    filter: blur(28px);
  }

  .footer__nav a {
    font-size: 15px;
  }

  .button-up__img {
    width: 53px;
    height: 53px;
  }

  .footer__phone {
    font-size: 16px;
  }

  .footer__inst::before {
    translate: -25% -25%;
    aspect-ratio: 1;
    opacity: .07;
    filter: blur(25px);
  }

  .footer__inst svg {
    width: 32px;
    height: 32px;
  }
}

@media (max-width: 320px) {
  .header {
    padding: 30px 1rem;
  }

  .logo {
    font-size: 1.2rem;
  }

  .button {
    overflow: visible;
    max-width: 200px;
    text-align: center;
    padding: 0.5714285714rem 1.8571428571rem;
  }

  .video-button__sprite {
    width: 14px;
    height: 12px;
  }

  .video-button__text {
    font-size: 0.7857142857rem;
  }

  .hero__title {
    font-size: 2.5714285714rem;
  }

  .hero__descr {
    font-size: 0.7857142857rem;
  }

  .hero__button-wrapper {
    flex-direction: column;
  }

  .stat__item {
    max-width: 80px;
  }

  .stat__number {
    font-size: 2rem;
  }

  .stat__descr {
    font-size: 0.7857142857rem;
  }

  .we-have {
    padding: 4.2857142857rem 1rem 0;
  }

  .we-have__cards {
    border-radius: 1.4285714286rem;
    padding: 44px 20px;
    gap: min(1.2rem,4vw);
    max-width: 100%;
  }

  .we-make {
    padding: 4rem 1rem 0;
  }

  .we-make__container {
    gap: 3rem;
  }

  .we-make__have-marker {
    position: absolute;
    right: 0;
    top: 0;
    translate: -50% -50%;
