  }

  .we-make__info {
    gap: 2.2857142857rem;
  }

  .we-make__lines--moby {
    display: none;
  }

  .we-make__title {
    font-size: 2.5714285714rem;
  }

  .we-make__descr {
    font-size: 0.8571428571rem;
  }

  .slider {
    gap: 2rem;
    padding: 0 3.3571428571rem 0 1.9285714286rem;
  }

  .events__title {
    font-size: 2.5714285714rem;
  }

  .contacts__title {
    font-size: 2.5714285714rem;
  }

  .contacts__descr {
    font-size: 0.8571428571rem;
  }

  .contact__inst {
    margin-right: 8px;
  }

  .contact__phone {
    margin-right: 21px;
  }

  .contact__text {
    font-size: 1rem;
  }
}

@media (prefers-reduced-motion: reduce) {
  *,
  ::before,
  ::after {
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: .01ms !important;
    scroll-behavior: auto !important;
  }
}